<template>
	<view>
		<!-- 顶部搜索框 -->
		<view class="topSerchView">
			<view class="SearchBar" elevation='5px' style="">
				<input class="addressInput" @input="onInput" placeholder="请输入关键字搜索" />
			</view>
		</view>
		<!-- 搜索列表 -->
		<view class="stationList" :style="{ 'height':scrollHeight }" v-if="isShowList">
			<block v-for="(item,index) in keywordList" :key="index">
				<view class="listItem" @tap="itemClick(index)">
					<rich-text :nodes="item.htmlStr"></rich-text>
				</view>
			</block>
		</view>
		<!-- 联动列表 -->
		<view class="list_box" v-if="isShowAllList">
			<!-- 左边的列表 -->
			<view class="left">
				<scroll-view scroll-y="true" :style="{ 'height':scrollHeight }">
					<view class="item" 
						v-for="(item,index) in stationArray"
						:key="index" 
						:class="{ 'active':index==leftIndex }" 
						:data-index="index"
						@tap="leftTap"
					>{{item.ci_cityName}}</view>
		        </scroll-view>
			</view>
			<!-- 右边的列表 -->
			<view class="main" v-if="isShowAllList">
				<swiper class="swiper" :style="{ 'height':scrollHeight }" 
					:current="leftIndex" @change="swiperChange"
					 vertical="true" duration="300">
					<swiper-item v-for="(item,index) in stationArray" :key="index">
						<scroll-view  scroll-y="true" :style="{ 'height':scrollHeight }">
							<view class="item">
								<view class="goods" v-for="(item2,index2) in mainArray" :key="index2" @tap="detailStationTap(item2)">
									<view>
										<view>{{item2.ct_dedicatedLine}}</view>
									</view>
								</view>
							</view>
						</scroll-view>
					</swiper-item>
				</swiper>
			</view>
		</view>
	</view>
</template>

<script>
	import $bcfw from '@/common/BCFW/bcfw.js'
	export default {
		data() {
			return {
				scrollHeight:'500px',
				stationArray:[],
				leftArray:[],
				mainArray:[],
				leftIndex:0,
				keywordList: [],
				isShowAllList:true,//是否显示联动列表
				isShowList:false,//是否显示站点列表
				stationType:'',//判断上个页面点击的是上车点还是下车点
			}
		},
		onLoad(param){
			var that = this;
			// console.log(param);
			that.stationType = param.station;
			//获取站点列表
			that.getBusStationList();
			// that.routeInit();
			/* 设置当前滚动容器的高，若非窗口的高度，请自行修改 */
			uni.getSystemInfo({
				success:(res)=>{
					this.scrollHeight=`${res.windowHeight}px`;
				}
			});
		},
		methods: {
			//-------------------------获取车站列表数据-------------------------
			getBusStationList() {
				uni.showLoading();
				uni.request({
					url:$bcfw.Interface.fw_selectSpecialLine.value,
					method:$bcfw.Interface.fw_selectSpecialLine.method,
					header:{'content-type': 'application/json'},
					success: (res) => {
						console.log(res)
						uni.hideLoading();
						let that = this;
						// console.log(res.data);
						if (res.data.length != 0) {
							for (var i = 0; i < res.data.data.length; i++) {
								var cityNameArray = {
									ci_cityName : res.data.data[i].ci_cityName
								}
								this.stationArray.push(cityNameArray);
								for (var j = 0; j < res.data.data[i].Countys.length;j++) {
									var countysArray = {
										ct_dedicatedLine : res.data.data[i].Countys[j].ct_dedicatedLine,
										ct_latitude : res.data.data[i].Countys[j].ct_latitude,
										ct_longitude : res.data.data[i].Countys[j].ct_longitude,
										ct_EndAddress : res.data.data[i].Countys[j].ct_EndAddress,
									}
									this.mainArray.push(countysArray);
								}
							}
						}
					},
					fail(res) {
						uni.hideLoading();
					}
				})
			},
			//-------------------------监听输入-------------------------
			onInput(event){
				var keyword = event.detail?event.detail.value:event;
				if (!keyword) {
					this.keywordList = [];
					this.isShowList = false;
					this.isShowAllList = true;
					return;
				}
				this.isShowList = true;
				this.isShowAllList = false;
				//以下示例截取淘宝的关键字，请替换成你的接口
				uni.showLoading();
				uni.request({
					url:$bcfw.Interface.fw_privateLineSearch.value,
					method:$bcfw.Interface.fw_privateLineSearch.method,
					header:{'content-type': 'application/json'},
					data:{
						linename:keyword,
						keyword:keyword
					},
					success: (res) => {
						uni.hideLoading();
						// console.log(res);
						this.keywordList = [];
						// console.log(res.data.data)
						this.keywordList = this.drawCorrelativeKeyword(res.data.data, keyword);
						
					},
					fail(res) {
						uni.hideLoading();
					}
				});
			},
			//-------------------------高亮关键字-------------------------
			drawCorrelativeKeyword(keywords, keyword) {
				// console.log(keywords);
				// var len = keywords.length;
				// console.log(len2);
				var	keywordArr = [];
				for (var i = 0; i < keywords.length; i++) {
					for(var j = 0; j < keywords[i].Countys.length; j++){
						// var row = keywords[i].Countys[i].ct_dedicatedLine;
					
						var row = {
							ct_dedicatedLine :keywords[i].Countys[j].ct_dedicatedLine,
							ct_latitude :keywords[i].Countys[j].ct_latitude,
							ct_longitude :keywords[i].Countys[j].ct_longitude,
							ct_EndAddress : keywords[i].Countys[j].ct_EndAddress,
						}
						//定义高亮#9f9f9f
						// console.log(row);
						 var html = row.ct_dedicatedLine.replace(keyword, "<span style='color: #9f9f9f;'>" + keyword + "</span>");
						html = '<div>' + html + '</div>';
						var tmpObj = {
							keyword: row,
							htmlStr: html
						};
						keywordArr.push(tmpObj)
						// console.log(keywordArr)
					}
				}
				return keywordArr;
			},
			//-------------------------点击下拉站点-------------------------
			itemClick(index){
				var that = this;
				//获取点击选项的文字
				var key = that.keywordList[index].keyword;
				if (that.stationType == 'dedicatedLine') {
					//当前是上车点
					uni.$emit('startstaionChange', {
					    data: key
					});
					uni.navigateBack({ });
				}
			},
			//-------------------------点击专线-------------------------			detailStationTap(item){				// console.log(item.countys);				var that = this;				if (that.stationType == 'dedicatedLine') {					//当前是专线					uni.$emit('startstaionChange', {					    data:item					});					uni.navigateBack({											});				}			},
			
			//-------------------------左侧导航点击-------------------------
			leftTap(e){
				let index=e.currentTarget.dataset.index;
				this.leftIndex=Number(index);
			},
			/* 轮播图切换 */
			swiperChange(e){
				let index=e.detail.current;
				this.leftIndex=Number(index);
			}
		}
	}
</script>

<style lang="scss">
	.topSerchView {
		height: 100rpx;
		background-color: #DBDBDB;
		padding-top: 20rpx;
	}
	.SearchBar {
		background-color: #FFFFFF;
		margin-right: 20rpx;
		margin-left: 20rpx;
		box-shadow: 0px 4px 9px 1px rgba(204, 204, 204, 1);
		flex-direction: row;
		border-radius: 9px;
		padding: 20rpx;
		align-items: center;
		justify-content: space-between;
	}
	//地址搜索输入
	.addressInput {
		color: #999999;
		font-size: 30rpx;
		font-weight: 300;
	}
	//站点列表
	.stationList {
		background-color: #FFFFFF;
		line-height: 80rpx;
		box-sizing: border-box;
		font-size: 28rpx;
		height: 100rpx;
		.listItem{
			margin-left: 20rpx;
			border-bottom: 1rpx solid #eeeeee;
		}
	}
.list_box{
	display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
	font-size: 28rpx;
	
	.left{
		width: 200rpx;
		background-color: #f6f6f6;
		line-height: 80rpx;
		box-sizing: border-box;
		font-size: 32rpx;
		text-align: center;
		.item{
			padding-left: 20rpx;
			position: relative;
			&:not(:first-child) {
				margin-top: 1px;
			
				&::after {
					content: '';
					display: block;
					height: 0;
					border-top: #d6d6d6 solid 1px;
					width: 620upx;
					position: absolute;
					top: -1px;
					right: 0;
					transform:scaleY(0.5);	/* 1px像素 */
				}
			}
			
			&.active,&:active{
				color: #42b983;
				background-color: #fff;
			}
		}
	}
	.main{
		background-color: #fff;
		padding-left: 20rpx;
		width: 0;
		flex-grow: 1;
		box-sizing: border-box;
		
		.swiper{
			height: 500px;
		}

		
		.item{
			padding-bottom: 10rpx;
		}
	}
	.goods{
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: flex-start;
		align-items: center;
		align-content: center;
		margin-bottom: 10rpx;
		border-bottom: 1rpx solid #eeeeee;
		line-height: 80rpx;
	}
}
</style>